@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Rajdhani:wght@300;400;500;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
  box-sizing: border-box;
  margin:0 ;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}
body{
background: #000;  
}
h1,h4,p,a{
color: #fff;
text-decoration: none;

}

ul{
  list-style-type: none;
}

.btn{
  padding:  12px 32px;
  font-size:  1rem;
  text-transform: uppercase;
  background: rgb(191, 88, 223);
  color: #222;
border: 1px solid #fff;
font-weight: 600;
cursor: pointer;
}

.btn-light{
  background: transparent;
  color: #fff;
}

 .btn:hover{
  background: rgba(255, 255,255, 0.2);
  color: #fff;
  transition: 0.3s;
 }