.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);

}

.footer-content {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
     gap: 40px 40px;
    /* display: flex;
    justify-content: space-around;
    align-items: center; */
}

.left {

    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
   

}

.location{
    display: flex;
    margin-bottom: 0.7rem;
}
.location p{
    line-height: 30px;

}

.right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}

.social{
    margin-top: 1rem;
}

h4{
   font-size: 1.2rem;
   padding-bottom:0.7rem ; 
}
p{
    font-size: 1.2rem;
}

@media screen and (max-width: 640px){
  .footer-content {
    grid-template-columns: 1fr;
}

}